/* Popup.css */

/* Overlay to cover the screen */
.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Transparent dark background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  
  /* Popup content box */
  .popup-content {
    background-color: white;
    width: 70%;
    height: auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow effect for top layer */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  /* Title styles */
  .popup-title {
    font-size: 24px;
    margin-bottom: 10px;
    text-align: center;
  }
  
  /* Question text styles */
  .popup-question {
    font-size: 18px;
    margin-bottom: 15px;
    text-align: center;
  }
  
  /* Styles for options */
  .popup-options {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .popup-option {
    background-color: #f0f0f0;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .popup-option:hover {
    background-color: #e0e0e0;
  }
  
  .popup-option.correct {
    background-color: #4caf50;
    color: white;
  }

  .popup-option.wrong {
    background-color: #F08080;
    color: white;
  }
  
  /* Proceed button styles */
  .popup-proceed {
    align-self: flex-end;
    padding: 10px 20px;
    background-color: #1E90FF;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .popup-proceed:hover {
    background-color: #87CEEB;
  }
  